import { Component } from '@angular/core';
import { IonicPage, LoadingController, NavController, ToastController } from 'ionic-angular';
import { User } from '../../providers';
import { Storage } from '@ionic/storage';
import { TranslateService } from '@ngx-translate/core';
import { MainPage } from '../index';
import { ToastProvider } from '../../providers/toast/toast';

/**
 * The Welcome Page is a splash page that quickly describes the app,
 * and then directs the user to create an account or log in.
 * If you'd like to immediately put the user onto a login/signup page,
 * we recommend not using the Welcome page.
*/
@IonicPage({
  name: 'WelcomePage'
})
@Component({
  selector: 'page-welcome',
  templateUrl: 'welcome.html'
})
export class WelcomePage {

  // The account fields for the login form.
  // If you're using the username field with or without username, make
  // sure to add it to the type
  account: { username: string, password: string } = {
    username: '',
    password: ''
  };

  // Our translated text strings
  private loginErrorString: string;

  constructor(public navCtrl: NavController,
              public user: User, private storage: Storage,
              public toastCtrl: ToastController,
              private toastPrv: ToastProvider,
              private loadingCtrl: LoadingController,
              public translateService: TranslateService) {

    this.translateService.get('LOGIN_ERROR').subscribe((value) => {
      this.loginErrorString = value;
    });

    this.initForm();
  }

  ionViewDidLoad() {
    this.user.logout();
  }

  async initForm() {
    this.account.username = await this.storage.get('email');
    console.log('this.account.username', this.account.username);
  }

  // Attempt to login in through our User service
  doLogin() {

    if (!this.account.username) {
      this.toastPrv.show('Informe o e-mail');
      return;
    }

    if (!this.account.password) {
      this.toastPrv.show('Informe sua senha');
      return;
    }

    let loading = this.loadingCtrl.create({
      content: 'Por favor aguarde...'
    });

    loading.present();

    this.user.login(this.account).then((resp) => {
      loading.dismiss();
      this.storage.set('email', this.account.username);
      this.navCtrl.setRoot(MainPage);
    }, (err) => {
      loading.dismiss();
      // this.navCtrl.push(Login);
      // Unable to log in
      let toast = this.toastCtrl.create({
        message: this.loginErrorString,
        duration: 3000,
        position: 'top'
      });
      toast.present();
    });
  }

  // login() {
  //   this.navCtrl.push('LoginPage');
  // }
  //
  // signup() {
  //   this.navCtrl.push('SignupPage');
  // }
}



